@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --unbuntu: 'Ubuntu', sans-serif
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, 
body {
  height: 100%;
}

body {
  font-family: 'Ubuntu', sans-serif;
  font-family: var(--unbuntu);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  background-color: #2196F3;
}

.profile-card {
  width: 600px;
  height: 800px;
  top: 50%;
  left: 50%;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  background-color: #64B5F6;
  margin: 150px auto;
}

.profile {
  height: auto;
  margin: 70px auto;
}

.img-container {
  max-width: 100%;
}

.profile-img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: none;
}

.greeting {
  font-family: 'Ubuntu', sans-serif;
  font-family: var(--unbuntu);
  font-size: 40px;
  margin-bottom: 15px;
  color: white;
}

.placeholder {
  font-size: 35px;
  color: white;
}

.btns {
  display: -webkit-flex;
  display: flex;
}

.btns button {
  margin-top: 2rem;
  height: 3rem;
  width: 9rem;
  border: none;
  border-radius: 1.5rem;
  font-size: 1rem;
  font-weight: 600;
  margin-right: 1rem;
  cursor: pointer;
  color: white;
  background: #2196F3;
}

.btns button:hover {
  box-shadow: rgba(3, 102, 214, 0.3) 0px 0px 0px 3px;
}

.like-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 30px;
}

.likes {
  margin: 10px;
  background-color: #64B5F6;
  font-size: 30px;
  border: none;
}

.likes:hover {
  cursor: pointer;
}

.footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 20px;
}

.social {
  margin: 10px;
}

@media screen and (max-width: 450px) {
  body {
    max-width: 100vw;
    height: 100vh;
  }
  
  .profile-card {
    max-width: 95%;
    height: 90%;
  }

  .profile {
    margin: 40px;
  }

  .footer {
    padding-top: 0;
  }
}

@media screen and (max-width: 360px) {
  .profile-img {
    width: 150px;
    height: 150px;
  }

  .profile {
    margin: 20px;
  }
}
